<template>
<div class="right-content">
    <el-row class="mb-10">
        <el-col :span="24" class="el-item display-table top-toolbar-complex">
            <div class="toolbar-path tn"></div>
            <div class="toolbar-function tr">
                <el-form :model="toolbarFrom" :inline="true" class="tr">
                    <el-form-item>
                        <el-button type="primary" @click="routerPush('create')"></el-button>
                    </el-form-item>
                    <el-form-item>
                        <el-input placeholder="" class="function-search" icon="search" v-model="toolbarFrom.searchkey" :on-icon-click="searchToolbar"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="searchToolbar"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-col>
    </el-row>

    <el-row class="mb-15">
        <el-col :span="24" class="el-item pa-10">
            <el-table :data="dataList.list" :stripe="true" class="w-100">
                <el-table-column prop="code" label="" width="100"></el-table-column>
                <el-table-column prop="name" label=""></el-table-column>
                <el-table-column prop="status" label="" width="100">
                    <template scope="scope">
                        <span :class="scope.row.status === '1' ? 'f-success' : '' ">{{ scope.row.status | enableStatus}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" width="80">
                    <template scope="scope">
                        <el-button type="primary" size="small" @click="routerPush(scope.row.id)"></el-button>
                        <!-- <el-button type="danger" size="small" @click="deleteItem(scope.row.id)"></el-button> -->
                    </template>
			  	</el-table-column>
			</el-table>

			<el-pagination @current-change="handleCurrentChange" :page-size="20" :current-page="1" layout="total, prev, pager, next, jumper" :total="dataList.count">
            </el-pagination>
		</el-col>
	</el-row>

</div>
</template>

<script type="text/javascript">
// 

export default {
    data() {
        return {
            toolbarFrom: {
                searchkey: ''
            }
        }
    },
    created() {
        this.getProvinceAdminList({ page: 1 })
    },
    computed: {
        dataList() { return this.$store.getters.provinceAdminList }
    },
    methods: {
        searchToolbar() {
            if(this.toolbarFrom.searchkey === '') {
                that.$alert('', '', { confirmButtonText: '', type: 'error' })
                return false
            }
            this.getProvinceAdminList({ page: 1, key: this.toolbarFrom.searchkey })
        },
        routerPush(id) {
            if(id === 'create') {
                this.$router.push({ name: 'administrativecreate', params: { type: 'p' } })
            } else {
                this.$router.push({ name: 'administrativeeditor', params: { id: id, type: 'p' } })
            }
        },
        deleteItem(id) {

        },
        handleCurrentChange(val) {
            if(this.toolbarFrom.searchkey !== '') {
                this.getProvinceAdminList({ page: val, key: this.toolbarFrom.searchkey })
            }else {
                this.getProvinceAdminList({ page: val })
            }
        },
        getProvinceAdminList(param) {
            this.$store.dispatch('getProvinceAdminList', param)
        }
    }
}
</script>



