<template>
<div id="community" class="right-content">
    <el-row class="mb-10">
        <el-col :span="24" class="el-item display-table top-toolbar-complex">
            <div class="toolbar-path tn"></div>
            <div class="toolbar-function tr">
                <el-form :model="toolbarFrom" :inline="true" class="tr">
                    <el-form-item>
                        <el-button type="primary" @click="routerPush('new')"></el-button>
                    </el-form-item>
                    <el-form-item>
                        <el-select v-model="toolbarFrom.progress" disabled placeholder=""></el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-select v-model="toolbarFrom.city" placeholder="">
                            <el-option label="" value="zhongshang"></el-option>
                            <el-option label="" value="guangzhou"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-select v-model="toolbarFrom.area" placeholder="/" no-data-text=""></el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="searchToolbar"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-col>
    </el-row>

    <el-row class="mb-10">
        <el-col :span="24" class="el-item pa-10">
            <el-table :data="tableData" :stripe="true" class="w-100">
                <el-table-column prop="progress" label="" width="100"></el-table-column>
                <el-table-column prop="city" label="" width="100"></el-table-column>
                <el-table-column prop="area" label="/" width="100"></el-table-column>
                <el-table-column prop="residential" label="/" width="150"></el-table-column>
                <el-table-column prop="address" label=""></el-table-column>
                <el-table-column prop="status" label="" width="100"></el-table-column>
                <el-table-column label="" width="80">
                    <template scope="scope">
                        <el-button type="primary" size="small" @click="routerPush(scope.row.id)"></el-button>
					</template>
			  	</el-table-column>
			</el-table>

            <el-pagination @current-change="handleCurrentChange" :current-page="1" layout="total, prev, pager, next, jumper" :total="tableData.length">
            </el-pagination>

		</el-col>
	</el-row>
</div>
</template>

<script type="text/javascript">
// 
import '../../static/style/boxment/community.scss'

export default {
    data() {
        return {
            toolbarFrom: {
                progress: '',
                city: '',
                area: ''
            },
            tableData: [{
                id: 1,
                progress: '',
                city: '',
                area: '',
                residential: '',
                address: '',
                status: ''
            },{
                id: 2,
                progress: '',
                city: '',
                area: '',
                residential: '',
                address: '',
                status: ''
            },{
                id: 3,
                progress: '',
                city: '',
                area: '',
                residential: '',
                address: '',
                status: ''
            },{
                id: 4,
                progress: '',
                city: '',
                area: '',
                residential: '',
                address: '',
                status: ''
            },{
                id: 5,
                progress: '',
                city: '',
                area: '',
                residential: '',
                address: '',
                status: ''
            },{
                id: 6,
                progress: '',
                city: '',
                area: '',
                residential: '',
                address: '',
                status: ''
            }]
        }
    },
    methods: {
        routerPush(id) {
            // 
            this.$router.push({ name: 'communityeditor', params: { id: id } })
        },
        handleCurrentChange(val) {
            // 
            console.log(`${val}`)
        },
        searchToolbar() {
             // 
            if(this.toolbarFrom.progress === '' && this.toolbarFrom.city === '' && this.toolbarFrom.area === ''){
                this.$alert('', '', { confirmButtonText: '' })
                return false
            }
        }
    }
}
</script>



