<template>
<div id="staffmenteditor" class="right-content">
    <topbar bone="" btwo=""></topbar>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item editor-form display-table">
            <div class="form-left">
                <el-form ref="form" :rules="rules" label-position="right" :model="form" label-width="150px">
                    <el-form-item label="" prop="name">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="password">
                        <el-input v-model="form.password"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="post">
                        <el-input v-model="form.post"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="phone">
                        <el-input v-model="form.phone"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-switch on-text="" off-text="" v-model="form.delivery"></el-switch>
                    </el-form-item>
                </el-form>
            </div>
            <div class="form-right">
                <el-upload action="//jsonplaceholder.typicode.com/posts/" :show-file-list="false" :on-success="handleAvatarScucess" :before-upload="beforeAvatarUpload" drag>
                    <img v-if="imageUrl" :src="imageUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
                <p class="mt-10"><br>jpg/png2mb</p>
            </div>
        </el-col>
    </el-row>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item pa-20">
            <el-dropdown trigger="click" @command="dropdownCity">
                <el-button>
                    {{ dropdown.city }}<i class="el-icon-caret-bottom el-icon--right"></i>
                </el-button>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command=""></el-dropdown-item>
                    <el-dropdown-item command=""></el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
            <el-dropdown trigger="click" @command="dropdownArea">
                <el-button>
                    {{ dropdown.area }}<i class="el-icon-caret-bottom el-icon--right"></i>
                </el-button>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command=""></el-dropdown-item>
                    <el-dropdown-item command=""></el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>

            <el-table :data="tableData" :stripe="true" class="w-100 mt-10">
                <el-table-column prop="provinces" label="" width="90"></el-table-column>
                <el-table-column prop="city" label="" width="90"></el-table-column>
                <el-table-column prop="area" label="" width="90"></el-table-column>
                <el-table-column prop="boxname" label="" width="150"></el-table-column>
                <el-table-column prop="address" label=""></el-table-column>
                <el-table-column type="selection" width="55"></el-table-column>
            </el-table>

            <el-pagination @current-change="handleCurrentChange" :current-page="1" layout="total, prev, pager, next, jumper" :total="tableData.length">
            </el-pagination>

        </el-col>
    </el-row>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item pa-20">
            <p class="theme-p mb-10"></p>
            <el-button type="primary" @click="submitForm('form')"></el-button>
            <cancel></cancel>
        </el-col>
    </el-row>
</div>
</template>

<script>
// 
import topbar from '../common/topbar.vue'
import cancel from '../common/cancel.vue'
import '../../static/style/staffment/tallymanEditor.scss'

export default {
    data() {
        return {
            imageUrl: '',
            form: {
                name: '',
                password: '',
                post: '',
                phone: '',
                delivery: false
            },
            rules: {
                name: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 3,
                        max: 20,
                        message: ' 3  20 ',
                        trigger: 'blur'
                    }
                ],
                password: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 5,
                        max: 20,
                        message: ' 5  20 ',
                        trigger: 'blur'
                    }
                ],
                post: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 5,
                        max: 20,
                        message: ' 5  20 ',
                        trigger: 'blur'
                    }
                ],
                phone: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 11,
                        max: 11,
                        message: '11',
                        trigger: 'blur'
                    }
                ]
            },
            tableData: [{
                provinces: '',
                city: '',
                area: '',
                boxname: 'A1001',
                address: ''
            }, {
                provinces: '',
                city: '',
                area: '',
                boxname: 'A1001',
                address: ''
            }, {
                provinces: '',
                city: '',
                area: '',
                boxname: 'A1001',
                address: ''
            }, {
                provinces: '',
                city: '',
                area: '',
                boxname: 'A1001',
                address: ''
            }, {
                provinces: '',
                city: '',
                area: '',
                boxname: 'A1001',
                address: ''
            }],
            dropdown: {
                city: '',
                area: ''
            },
            fileList: [],
            manid: this.$route.params.id
        }
    },
    // manidnew
    methods: {
        handleAvatarScucess(res, file) {
            this.imageUrl = URL.createObjectURL(file.raw);
        },
        beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
            const isLt2M = file.size / 1024 / 1024 < 2

            if (!isJPG) {
                this.$message.error(' JPG/PNG !')
            }
            if (!isLt2M) {
                this.$message.error(' 2MB!')
            }
            return isJPG && isLt2M;
        },
        dropdownCity(command) {
            this.dropdown.city = command
        },
        dropdownArea(command) {
            this.dropdown.area = command
        },
        handleCurrentChange(val) {
            console.log(`: ${val}`)
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {

                } else {
                    this.$alert('', '', { confirmButtonText: '' })
                }
            })
        }
    },
    components: {
        topbar,
        cancel
    }
}
</script>



