<template>
<div id="login">
    <el-row>
        <el-col :span="8" class="login-left">
            <h1 class="login-left-up"><img src="../../static/img/login-left-up.png"></h1>
            <img src="../../static/img/login-left-down.png" class="login-left-down">
        </el-col>
        <el-col :span="16" class="login-right">
            <div class="login-right-wrap">
                <h2>USER LOGIN</h2>
                <h1></h1>
                <el-form :model="form" :rules="rules" ref="form">
                    <el-form-item prop="username">
                        <el-input placeholder="" v-model="form.username" class="mt-20" size="large">
                            <template slot="prepend"></template>
						</el-input>
					</el-form-item>
					<el-form-item prop="password">
						<el-input type="password" placeholder="" v-model="form.password" class="mt-20" size="large">
						    <template slot="prepend"></template>
						</el-input>
					</el-form-item>
					<el-button type="primary" @click="onLogin('form')" class="login-login"></el-button>
				</el-form>
			</div>
		</el-col>
	</el-row>
</div>
</template>

<script type="text/javascript">
/**
 * 
 */
import '../../static/style/logins/login.scss'

// 
export default {
    data() {
        return {
            form: {
                username: '',
                password: ''
            },
            rules: {
                username: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 3,
                        max: 20,
                        message: ' 3  20 ',
                        trigger: 'blur'
                    }
                ],
                password: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 5,
                        max: 20,
                        message: ' 5  20 ',
                        trigger: 'blur'
                    }
                ]
            }

        }
    },
    methods: {
        onLogin(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // 
                    this.$store.dispatch('onLogin', this.form)
                } else {
                    this.$alert('', '', { confirmButtonText: '' })
                }
            });
        }
    }
}
</script>



