<template>
<div id="staffmenteditor" class="right-content">
    <topbar bone="" :btwo="btwo"></topbar>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item editor-form display-table">
            <div class="form-left">
                <el-form ref="form" :rules="rules" label-position="right" :model="form" label-width="150px">
                    <el-form-item label="" prop="progress">
                        <el-select v-model="form.progress" placeholder="">
                              <el-option label="" value=""></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="city">
                        <el-select v-model="form.city" placeholder="" no-data-text="">
                            <el-option label="" value=""></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="area">
                        <el-select v-model="form.area" placeholder="" no-data-text="">
                            <el-option label="" value=""></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="address">
                        <el-input v-model="form.address"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="residential">
                        <el-input v-model="form.residential"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-switch on-text="" off-text="" v-model="form.delivery"></el-switch>
                    </el-form-item>
                </el-form>
            </div>
            <div class="form-right"></div>
        </el-col>
    </el-row>

    <el-row class="mb-10">
        <el-col :span="24" class="el-item pa-20">
            <p class="theme-p mb-10"></p>
            <el-button type="primary" @click="submitForm('form')"></el-button>
            <cancel></cancel>
        </el-col>
    </el-row>
</div>
</template>

<script>
// 
import topbar from '../common/topbar.vue'
import cancel from '../common/cancel.vue'
import '../../static/style/boxment/communityEditor.scss'

export default {
    data() {
        return {
            param: this.$route.params.id,
            btwo: '',
            form: {
                progress: '',
                city: '',
                area: '',
                address: '',
                residential: '',
                delivery: false
            },
            rules: {
                progress: [{
                    required: true,
                    message: '',
                    trigger: 'change'
                }],
                city: [{
                    required: true,
                    message: '',
                    trigger: 'change'
                }],
                area: [{
                    required: true,
                    message: '',
                    trigger: 'change'
                }],
                address: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }
                ],
                residential: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }
                ]
            }
        }
    },
    created() {
        this.btwo = this.param === 'new' ? '' : ''
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    console.log('')
                } else {
                    this.$alert('', '', { confirmButtonText: '' })

                    console.log('error submit!!')
                }
            })
        }
    },
    components: {
        topbar,
        cancel
    }
}
</script>



