<template>
<div id="boxmentlist" class="right-content">
    <el-row class="mb-10">
        <el-col :span="24" class="el-item display-table top-toolbar-complex">
            <div class="toolbar-path tn"></div>
            <div class="toolbar-function tr">
                <el-button type="primary" @click="routerPush('new')"></el-button>
                <el-select v-model="progress" disabled placeholder=""></el-select>
                <el-select v-model="city" placeholder="">
                    <el-option label="" value="zhongshang"></el-option>
                    <el-option label="" value="guangzhou"></el-option>
                </el-select>
                <el-select v-model="area" placeholder="/" no-data-text=""></el-select>
                <el-button type="primary" @click="searchToolbar"></el-button>
            </div>
        </el-col>
    </el-row>

    <el-row class="mb-10">
        <el-col :span="24" class="el-item pa-10">
            <el-table :data="boxlist" :stripe="true" class="w-100" :row-class-name="tableRowDisabled">
                <el-table-column prop="box_no" label="" width="100"></el-table-column>
                <el-table-column prop="province" label="" width="100"></el-table-column>
                <el-table-column prop="city" label="" width="100"></el-table-column>
                <el-table-column prop="area" label="/" width="100"></el-table-column>
                <el-table-column prop="community" label="/" width="150"></el-table-column>
                <el-table-column prop="addr" label=""></el-table-column>
                <el-table-column prop="status" label="" width="100">
                    <template scope="scope">
                        <span :class="scope.row.status === '1' ? 'f-success' : '' ">{{ scope.row.status | enableStatus}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" width="80">
                    <template scope="scope">
                        <el-button type="primary" size="small" @click="routerPush(scope.row.id)"></el-button>
					</template>
			  	</el-table-column>
			</el-table>
            <el-pagination @current-change="handleCurrentChange" :page-size="20" :current-page="1" layout="total, prev, pager, next, jumper" :total="boxlist.length">
            </el-pagination>
		</el-col>
	</el-row>

</div>
</template>

<script type="text/javascript">
// 
import '../../static/style/boxment/boxlist.scss'
export default {
    data() {
        return {
            toolbarFrom: {
                progress: '',
                city: '',
                area: ''
            }
        }
    },
    created() {
        this.$store.dispatch('getBoxList')
    },
    computed: {
        boxlist() { return this.$store.getters.boxList }
    },
    methods: {
        routerPush(id) {
            console.log(id)
            this.$router.push({ name: 'boxeditor', params: { id: id } })
        },
        handleCurrentChange(val) {

        },
        searchToolbar() {

        },
        tableRowDisabled(row, index) {
            // 
            if(row.status == 0) {
                return 'row-disabled'
            }
        }
    }
}

</script>



